.container {

    background-color: $container-color;
    color:$home-color;
    height:85vh;
    overflow-y:auto;
    font-family: verdana;
    display:flex;
   
    flex-wrap:wrap;
    align-content: flex-start;
}

.child {
    border-radius:8px;
    margin-left:1.11111%;
